.wp-block-mrs-block-mrs-block-testimonials {
	border: 1px dotted #f00;
	margin: 0;

	.block-editor-inner-blocks {
		.block-editor-block-list__layout {


			[data-type="mrs-block/mrs-block-testimonial"] {
				.mrs-block-testimonials-container * {
					-webkit-box-sizing: border-box;
					box-sizing: border-box;
					-webkit-transition: all 0.35s cubic-bezier(0.25, 0.5, 0.5, 0.9);
					transition: all 0.35s cubic-bezier(0.25, 0.5, 0.5, 0.9);
				}
				.mrs-block-testimonials-container {
					font-family: 'Raleway', Arial, sans-serif;
					position: relative;
					overflow: hidden;
					margin: 10px;
					min-width: 220px;
					max-width: 310px;
					width: 100%;
					color: #333;
					text-align: left;
					box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
					border-radius: 8px;

					img {
						max-width: 100%;
						vertical-align: middle;
						}
					.mrs-block-testimonials-container-quote {
						position: relative;
						blockquote {
							background-color: #ffffff;
							padding: 25px 50px 25px 50px;
							font-size: 0.8em;
							font-weight: 500;
							text-align: left;
							margin: 0;
							line-height: 1.6em;
							font-style: italic;
	
							&::before,
							&::after {
								font-family: 'FontAwesome';
								content: "\201C";
								position: absolute;
								font-size: 50px;
								opacity: 0.3;
								font-style: normal;
								}
							&::before {
								top: 25px;
								left: 20px;
							}
							&::after {
								content: "\201D";
								right: 20px;
								bottom: 0;
							}
						}
						.arrow {
							top: 100%;
							left: 48px;
							width: 0;
							height: 0;
							border-left: 0 solid transparent;
							border-right: 25px solid transparent;
							border-top: 25px solid #ffffff;
							margin: 0;
							position: absolute;
						}
					}					
					.block-editor-media-placeholder {
						min-height: 250px;
					}
					.author {
						position: absolute;
						bottom: 0;
						width: 100%;
						padding: 5px 25px;
						color: #000000;
						background-color: #ffffff;
						margin: 0;
						text-transform: uppercase;
						h5 {
							display: inline-block;
							opacity: 0.8;
							margin: 0;
							font-weight: 800;
						}
						span.mrs-block-testimonial-profession {
							font-weight: 400;
							text-transform: none;
							font-size: 12px;
							padding-left: 10px;
						}
					}
				}
			}
			.block-list-appender {
				width: 200px;
				border: 1px dashed #555;
				min-height: 70px;
				display: flex;
				justify-content: center;
				align-items: center;
				position: relative;
			}
		}
	}
}
